import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'// 
import Baojie from '@/pages/baojie/baojie'// 
import Rchang from '@/pages/baojie/rchang'// 
import Border from '@/pages/baojie/border' //

Vue.use(Router)

const router= new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path:'/baojie',
      name:'baojie',
      component:Baojie,
      meta:{
        title:''
      }
    },
    {
      path:'/rchang',
      name:'rchang',
      component:Rchang,
      meta:{
        title:''
      }
    },
    {
      path:'/Border',
      name:'border',
      component:Border,
      meta:{
        title:''
      }
    }

  ]
})

router.beforeEach((to, from, next) => {
  //title
  changeTitle(to.meta.title)
  next();
});
//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
}
export default router


